<template>
  <div class="course_container">
    <!-- 头部部分 -->
    <div class="course_header" :class="flag?'course_header_top':'course_header'">
      <div style="color:#595959;font-size:16px;">&lt;</div>
      <div style="color:#595959;font-size:17px;">
        <div v-show="!flag">课程详情</div>
        <div v-show="flag" style="display:flex;jsutify-content:space-around;align-items:center;">
          <p>课程介绍</p>
          <p>课程大纲</p>
          <p @click="toCommentClick">课程评价</p>
        </div>
      </div>
      <div @click="erClick">
        <van-icon name="service" />
      </div>
    </div>
    <!-- basis部分 -->
    <div class="wrapper">
      <div class="basis_container">
        <div class="bsis_title">
          <div>{{this.infoData.title}}</div>
          <div style="color:orange;margin:5px 5px;">{{this.infoData.price|price}}</div>
          <div
            style="color:rgba(0,0,0,.45);font-size:13px;margin:5px 5px;"
          >共{{this.infoData.status}}课时|{{this.infoData.sales_num}}人报名</div>
        </div>
        <div class="start">
          <van-icon :color="colorShow?'orange':'#999'" name="star-o" @click="collectClick" />
        </div>
      </div>
      <!-- 教学部队部分 -->
      <div class="teach_container" v-for="(item,index) in this.infoData.teachers" :key="index">
        <div class="teach_top">教学团队</div>
        <div>
          <img
            style="width:40px;height:40px;border-radius:50%;margin:10px 10px;"
            :src="item.avatar"
            alt
          />
        </div>
        <div style="margin:5px 10px;color:#595959;font-size:14px;">{{item.teacher_name}}</div>
      </div>
      <!-- 课程介绍部分 -->
      <div class="course_part">
        <div class="course_part_top">课程介绍</div>
        <div
          class="course_part_main"
          style="margin:8px 8px "
          v-for="(item,index) in chapterData"
          :key="index"
        >{{item.periods_title}}</div>
      </div>
      <!-- 课程介绍 -->
      <div class="course_part">
        <div class="course_part_top">课程大纲</div>
        <div
          class="course_part_main"
          style="margin:8px 8px;color:#595959; "
          v-for="(item,index) in chapterData"
          :key="index"
        >{{item.periods_title}}</div>
      </div>
      <!-- 课程评价 -->
      <div class="course_comment" ref="comment">
        <div style="color:#333333;font-size:13px;">课程评价</div>
        <div class="comment_item" v-for="(item,index) in commentData.list" :key="index">
          <div>
            <img :src="item.avatar" alt />
          </div>
          <div class="comment_info">
            <div style="width:100%;height:30px;">{{item.nickname}}</div>
            <div style="width:100%;height:30px;">{{item.content}}</div>
          </div>
          <div></div>
        </div>
      </div>
    </div>
    <!-- 按钮 -->
    <div class="course_button" @click="applyClick">
      <van-button color="orange" style="width:90%" text="立即报名"></van-button>
    </div>
    <div style="position:fixed;top:250px;left:150px;" v-show="erShow">
    <qriously :value="initQCode" :size="100" />
    </div>
  </div> 
</template>

<script>
export default {
  components: {},
  data() {
    return {
      erShow:false,
      infoData: {},
      chapterData: [],
      commentData: [],
      colorShow: false,
      flag: true,
      initQCode: 'http://localhost:8080/courseDetails',

    };
  },
  computed: {},
  filters: {
    price(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `${(val / 100).toFixed(2)}`;
      }
    }
  },
  methods: {
    // 显示二维码
    erClick(){
      this.erShow=true;
    },
    // 报名
    applyClick(){
      this.$http.post("/api/app/order/downOrder",{
        shop_id:this.$route.query.id,
        type:3
      }).then((res)=>{
        console.log(res.data);
        if(res.data.code==200){
          this.change=true
        }
      })
    },
    // 锚点事件
    toCommentClick(){
      this.$refs.comment.scrollIntoView();
    },
    // 吸顶问题
    handleTop() {
      let distence = window.pageYOffset;
      this.flag = distence > 5;
    },
    collectClick() {
      this.colorShow = !this.colorShow;
      if (this.colorShow) {
        this.$http
          .post("/api/app/collect", {
            course_basis_id: this.infoData.info.id,
            type: 1
          })
          .then(resp => {
            // console.log(resp.data);
          });
      } else {
        this.$http.put("/api/app/collect/cancel/242/1").then(resp => {
          // console.log(resp.data);
        });
      }
    }
  },
  mounted() {
    // 默认回到顶部
    document.body.scrollTop = document.documentElement.scrollTop = 0
    // 吸顶问题
    window.addEventListener("scroll", this.handleTop, true);
    this.$http
      .get(`/api/app/courseInfo/basis_id=${this.$route.query.id}?`)
      .then(res => {
        // console.log(res.data.data);
        this.infoData = res.data.data.info;
      });


    this.$http
      .post("/api/app/courseChapter", { id: this.$route.query.id })
      .then(res => {
        // console.log(res.data.data);
        this.chapterData = res.data.data;
      });
    this.$http
      .post("/api/app/courseComment", {
        page: 1,
        limit: 10,
        id: this.$route.query.id
      })
      .then(res => {
        // console.log(res.data);
        this.commentData = res.data.data;
      });
  },
};
</script>
<style  scoped>
.course_button{
  width:100%;
  height:50px;
  display: flex;
  justify-content: center;
  align-items:Center;
  position:fixed;
  bottom:0px;
}
.wrapper {
  width: 100%;
  height: 680px;
  background: #f0f2f5;
}
.course_container {
  width: 100%;
}
.course_header {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.course_header_top {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #fff;
}
.basis_container {
  width: 100%;
  height: 110px;
  display: flex;
  background: #fff;
}
.bsis_title {
  width: 85%;
  height: 100%;
}
.teach_container {
  width: 100%;
  height: 120px;
  background: #fff;
  margin: 10px 0px;
}
.teach_top {
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
}
.course_part {
  width: 100%;
  height: 80px;
  background: #fff;
  margin-top: 10px;
  color: #262626;
  font-size: 13px;
}
.start {
  width: 20%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.course_comment {
  width: 100%;
  height: 110px;
  background: #fff;
  margin-top: 10px;
}
.comment_item {
  width: 100%;
  height: 45px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.comment_item img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.comment_info {
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>